import {React,useEffect }from 'react'
import kra2 from './kra2.png';
import nsa from './kra3.jpg';
import business from './business.png';
import wed2 from './wed2.png';
import poster from './poster.png';
import './Home.css';
import 'bootstrap/dist/css/bootstrap.css';
import { Button } from '../Button';
import { Link,useLocation } from 'react-router-dom'
import Sideposts from './Sideposts';
import person from './myimage1.png';
import logo2 from './mo.png';
import Typewriter from "typewriter-effect";
function Home() {
    
    return (
       <div className="homecolor pin lg-12">
           <div className='imgtext'>
           <img src={person} className="person" height=""alt="online cyber" width="10px" ></img>
           <div className='text-animate m-auto'>
           <h1>Cyber Services at The Click</h1>
           <h1> of A Button.</h1>
           <h1>We Provide The Following Cyber Services:</h1>
           <div className='typewriter'>
           <Typewriter 
  
  onInit={(typewriter)=> {

  typewriter
   
  .typeString("-KRA PIN Registration")
  .pauseFor(1000)
  .deleteAll()
  .typeString("-KRA Returns Filing")
  .pauseFor(1000)
  .deleteAll()
  .typeString("-E-Citizen Services")
  .pauseFor(1000)
  .deleteAll()
  .typeString("-TIMS NTSA Services")
  .pauseFor(1000)
  .deleteAll()
  .typeString("-Card Designs")
  .start();
  
  }}
  />
  </div>
           </div>
           </div>
            <div className="row ">
            <div className="col-lg-7 ">
        <div className=" ml-auto  homecard my-4 mx-3 ">
            <div className="grid-item ">
            <img src={kra2} height="200"alt="kra pin"width="100%"></img>
            </div>
            <div className="p-3 ">
            <h2 className="h4test">Register Kra Pin</h2>
            <p>Are you in need of a Kra Pin and wondering how to get it? worry no more.
                 Click the link below and follow the instructions given to send a pin register request to us.</p>
                 <Link to='kra'> <Button/>   </Link>
        </div>
        </div>
        <div className=" ml-auto pin homecard my-4 mx-3">
            <div className="grid-item">
            <img src={nsa} height="200"width="100%" alt='file kra returns'></img>
            </div>
            <div className="p-3">
            <h2 className="h4test">File Kra Returns</h2>
            <p>Whether you are employed or not, you are required by law to file tax returns yearly.
                Avoid  being fined for not filing returns by sending a returns filing request to us
                 </p>
                 <Link to='returns'> <Button/>   </Link>
        </div>
        </div>
        <div className=" ml-auto pin homecard my-4 mx-3">
            <div className="grid-item">
            <img src={business} height="200"width="100%"alt='business card'></img>
            </div>
            <div className="p-3">
            <h2 className="h4test">Business Card Design</h2>
            <p> A good business card helps to make your business stand out from the 
rest. Click below for proffesional business card design and you won't regret it.</p>
<Link to='business-card'> <Button/>   </Link>
        </div>
        </div>
        <div className=" ml-auto pin homecard my-4 mx-3">
            <div className="grid-item">
            <img src={wed2} height="200"width="100%"alt='wedding card'></img>
            </div>
            <div className="p-3">
            <h2 className="h4test">Wedding Card Design</h2>
            <p>Give us an opportunity to spice your wedding event by 
letting us design the wedding card you need.</p>
<Link to='wedding-card'> <Button/>   </Link>
        </div>
        </div>
        <div className="ml-auto pin homecard my-4 mx-3">
            <div className="grid-item">
            <img src={poster} height="200"width="100%"alt='poster design'></img>
            </div>
            <div className="p-3">
            <h2 className="h4test">Poster Design</h2>
            <p>Word has it that a good poster contributes much to the success of an event.We design eye catching posters at very pocket friendly costs.</p>
            <Link to='poster'> <Button/>   </Link>
        </div>
        </div>
        <div className="ml-auto pin homecard my-4 mx-3">
            <div className="grid-item">
            <img src={logo2} height="200"width="100%" alt='logo design'></img>
            </div>
            <div className="p-3">
            <h2 className="h4test">Logo Design</h2>
            <p>Your company won't stand out without the right Logo. Make a logo request by clicking the button below.</p>
<Link to='logo'> <Button/>   </Link>
        </div>
        </div>
        </div>
        <div className="col-lg-5">
        <Sideposts/>
        </div>
        </div>
     </div>
    )
}

export default Home
