import React, { Component } from 'react'
import { Card } from 'react-bootstrap';
import axios from "axios";
import { checkAuthenticated,load_user } from '../../actions/auth';
import {connect} from 'react-redux';
import {Link,Redirect} from 'react-router-dom';
import { withRouter } from 'react-router-dom';
export class Home extends Component {
    state = {
        requests : [],
    }
    componentDidMount() {
    
        let data ;
    
        axios.get(`/api/todos/`)
        .then(res => {
            data = res.data;
            this.setState({
                requests : data  
                 
            });
            console.Blog(data) 
        })
        .catch(err => {})

        this.props.checkAuthenticated();
     
    
        
    }
    
    
    render() {
        var heading = ['Town','date send','complete'];
        const { isAuthenticated } = this.props;
        if (isAuthenticated==false) {
            return (<Redirect to={'/login'} />);
        }
        return (
            <div className="mt-2 pl-5 col-lg-6 cardcol">
             
             <div className="row">
                {this.state.requests.slice(0, 1).map(detail => (
                 <Card style={{ width: '7rem' }}className="m-2 col text-center"bg="primary">
                 <Card.Body>
               
                   <Card.Text>
                    <h6 className="py-3"> TOTAL ARTICLES</h6>
                     
                     {detail.atotal
                     }
                     </Card.Text>
    
    </Card.Body>
  </Card>
                     
                     
                     
                  
             ))}
             {this.state.requests.slice(0, 1).map(detail => (
                 
                     
                 <Card style={{ width: '7rem' }}className="m-2 col text-center"bg="info">
                 <Card.Body>
               
                   <Card.Text>
                    <h6 className="py-3"> TOTAL JOB REQUESTS</h6>
                     
                     {detail.kratotal
                     }
                     </Card.Text>
    
    </Card.Body>
  </Card>
                     
                     
                  
             ))}
   {this.state.requests.slice(0, 1).map(detail => (
                 
                 <Card style={{ width: '7rem' }}className="m-2 col text-center"bg="secondary">
                 <Card.Body>
               
                   <Card.Text>
                    <h6 className="py-3"> TOTAL KRA REQUESTS</h6>
                     
                     {detail.jobtotal
                     }
                     </Card.Text>
    
    </Card.Body>
  </Card>
         ))}

   
    
     
</div>
            </div>
        )
            
         
    }
}
const mapStateToProps=state=>({
    isAuthenticated:state.auth.isAuthenticated
    });

export default  connect(mapStateToProps,{checkAuthenticated,load_user})(Home)
