import React,{useState,useEffect} from 'react';
import {Link,Redirect,BrowserRouter as Router,Route,Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import './Login.css'
import logo from './logo.png'
import { login } from '../../actions/auth';
import { checkAuthenticated,load_user } from '../../actions/auth';
import Admin from './Admin';
import validator from 'validator'

const Login=({login, isAuthenticated,checkAuthenticated,load_user})=> {
   const [formData,setformData]=useState({
email:'',
password:''
   });
   const [error,setError]=useState()
   const [emailError,setEmailError]=useState({})
   const [passwordError,setPasswordError]=useState({})
   const {email,password}= formData;
   const onChange=(e)=>setformData({...formData,[e.target.name]:e.target.value});
   const onSubmit=e=>{
       e.preventDefault();
       const isValid=formValidation();
       if(isValid){
       login(email,password);
       setError(isAuthenticated);
      setformData({})}
       }
   
  const formValidation=()=>{
const emailErr={};
const passwordErr={};
let isValid=true;
if(email.trim().length==0){
  emailErr.emailShort="Email is required";
  isValid=false;
}
if(!validator.isEmail(email)){
  emailErr.emailInvalid="Enter a valid Email";
  isValid=false;
}
if(password.trim().length==0){
  passwordErr.passwordShort="Password is required";
  isValid=false;
}
setEmailError(emailErr);
setPasswordError(passwordErr);
return isValid
  }
  
     
return(
  <div className='m-auto parentdiv'>
  <div className=' mt-5 logindiv loginScreen'>
      <h4 className=' centeritems mt-2'>MAMBO ONLINE</h4>
      <img src='' width='200'className='mx-auto mt-3 mb-3 centerlogo'></img>
      <hr/>
      <form onSubmit={e=>onSubmit(e)}> 
<div className='form-group controlsmargin'>
   {error==false && <h6 className='error' >wrong email or password</h6>}
<input
className='form-control '
type='text'
placeholder='Type your Email'
name='email'
value={email}
onChange={e=>onChange(e)}

/>
{Object.keys(emailError).map((key)=>{
  return <div style={{color:"red"}}>{emailError[key]}</div>
})}
</div>
<div className='form-group controlsmargin'>
<input
className='form-control mt-3 '
type='password'
placeholder='enter your password'
name='password'
value={password}
onChange={e=>onChange(e)}


/>
{Object.keys(passwordError).map((key)=>{
  return <div style={{color:"red"}}>{passwordError[key]}</div>
})}
</div>
<button className='btn btn-primary  mt-3 controlsmargin' type='submit'>Login</button>
      </form>
    <p className='mt-3 controlsmargin'>
        Forgot your Password?<Link to='/reset-password'>Reset Password</Link>
    </p>
  </div>
  </div>
);
};

const mapStateToProps=state=>({
   isAuthenticated:state.auth.isAuthenticated
   });

export default connect(mapStateToProps,{login,checkAuthenticated,load_user})(Login)
