import axios from 'axios';
import {
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    USER_LOADED_SUCCESS,
    USER_LOADED_FAIL,
    AUTHENTICATED_FAIL,
    AUTHENTICATED_SUCCESS,
    LOGOUT,
    IS_ACTIVE,
    NOT_ACTIVE
} from './types';
import axiosInstance from '../axios'
import { useHistory } from 'react-router';
import {Link,Redirect} from 'react-router-dom';


export const checkAuthenticated=()=> async dispach=>{
if(localStorage.getItem('access')){

    const body=JSON.stringify({token: localStorage.getItem('access')});
    try{
const res = axiosInstance.post('/jwt/verify/',body)
if((await res).data.code !== 'token not valid')
{
    dispach({
        type: AUTHENTICATED_SUCCESS
    });

}else{
    dispach({
        type: AUTHENTICATED_FAIL
    });
}
    }catch(err){
        dispach({
            type: AUTHENTICATED_FAIL
        });
    }
}
};

export const load_user=()=>async dispach=>{
if(localStorage.getItem('access')){
    const config={
        Headers:{
            'content-type':'application/json',
            'Authorization':`JWT ${localStorage.getItem('access')}`,
            'Accept':'application/json'
        }
        };
        try{
            const res= axios.get('/auth/users/me/',config);
            dispach({
                type: USER_LOADED_SUCCESS,
                payload: res.data
            })
            }catch(err){
                dispach({
                    type: USER_LOADED_FAIL,
                })
            }   
}else{
    dispach({
        type: USER_LOADED_FAIL,
    })  
}

};

export const login=(email,password)=>async dispach=>{
    
try{
  
const res= axiosInstance
.post('/jwt/create/',{
    email:email,
    password:password,
})
.then((ress)=>{
console.log(ress.data)

dispach({
    type: LOGIN_SUCCESS,
    payload: ress.data
})
dispach(load_user());
<Redirect to="/"/>;
})

      
}
catch(err){
    dispach({
        type: LOGIN_FAIL,
    })
    console.log(err);
    
}

};

export const logout=()=>dispach=>{
    dispach({
        type:LOGOUT
    });
};
export const checkActive=()=>dispach=>{

    if(window.location.pathname == '/poster'|window.location.pathname == '/business-card' |window.location.pathname == '/wedding-card' |window.location.pathname == '/logo'){
    dispach({
        type:IS_ACTIVE
    })}else{
        dispach({
            type:NOT_ACTIVE
        })
    }
};