import React, {useState} from 'react'
import {Menuitems} from './Menuitems'
import {NavLink} from 'react-router-dom'
import './Dropdown.css';

function Dropdown() {
    const[click, setClick]=useState(false);
    const handleClick=()=>setClick(!click)
    return (
        <>
           <ul onClick={handleClick} className={click? 'dropdown-menu2 clicked':'dropdown-menu2 '}>
               {Menuitems.map((item, index)=>{
                   return(
                   <li key={index}>
                       <NavLink className={item.cname} activeClassName="activel nav-links1" to={item.path} onClick={()=>setClick(false)}>
                           {item.title}
                       </NavLink>
                   </li>
                   );
               })}
               
               </ul> 
        </>
    )
}

export default Dropdown;
