import React, { Component } from 'react'
import axios from "axios";
import { Card,Modal} from 'react-bootstrap';
import {
  Button,
  Form,
  FormGroup,
  Input,
  Label,
} from "reactstrap";
export class Articles extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            requests : [],
            showHide : false,
            statuss:"",
            edit:false,
            id:"",
            activeItem: {
              title: "",
              body: "",
              photo: null,
              author: "",
              dateposted: this.handledate(),
              
            }
        }
       
    }
   
    componentDidMount() {
    
        let data ;
    
        axios.get("/api/article/")
        .then(res => {
            data = res.data;
            this.setState({
                requests : data  
                 
            });
            console.Blog(data) 
        })
        .catch(err => {})
    }
    handleChange = (e) => {
      let { name, value } = e.target;
  
      if (e.target.type === "file") {
        value = e.target.files[0];
      }
  
      const activeItem = { ...this.state.activeItem, [name]: value };
      this.setState({ activeItem });
  
      
    };
    handleModalShowHide() {
        this.setState({ showHide: !this.state.showHide})
    }
    closeModal() {
      this.setState({ showHide: false,edit:false})
      this.setState({ edit:false})
  }
    handleSubmit = (e) => {
      if(this.state.id){
        e.preventDefault();
        let formfield= new FormData()
        formfield.append('title',this.state.activeItem.title)
        formfield.append('body',this.state.activeItem.body)
        formfield.append('title',this.state.activeItem.title)
        formfield.append('photo',this.state.activeItem.photo)
        formfield.append('author',this.state.activeItem.author)
        formfield.append('dateposted',this.state.activeItem.dateposted)
        axios({
          method:'put',
          url:`/api/article/${this.state.id}/`,
          data:formfield,
        }).then((Response)=>{
          this.refreshList()
        })
        alert("Record Updated successfully");
      }
      else{
      e.preventDefault();
     let formfield= new FormData()
     formfield.append('title',this.state.activeItem.title)
     formfield.append('body',this.state.activeItem.body)
     formfield.append('title',this.state.activeItem.title)
     formfield.append('photo',this.state.activeItem.photo)
     formfield.append('author',this.state.activeItem.author)
     formfield.append('dateposted',this.state.activeItem.dateposted)
     axios({
       method:'post',
       url:'/api/article/',
       data:formfield,
     }).then((Response)=>{
      this.refreshList()
      this.clearFields()
      alert(this.state.activeItem.title)
     })
     alert("Record Saved successfully");
    }
    };
    
    handledate = () => {
      var today = new Date(),
      date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
      return date;
    }
    editItem = (detail) => {
      this.setState({ id: detail.id, showHide: !this.state.showHide,edit:true,activeItem:detail });
    };
    handleDelete = (item) => {
      axios({
        method:'delete',
        url:`/api/article/${item.id}/`,
      }).then((Response)=>{
        this.refreshList()
        alert("Record deleted successfully");
      })
    };
    refreshList = () => {
      let data ;
    
        axios.get("/api/article/")
        .then(res => {
            data = res.data;
            this.setState({
                requests : data  
                 
            });
            console.Blog(data) 
        })
        .catch(err => {})
    };
    clearFields = () => {
      this.setState({
        title: "",
        body: "",
        photo: null,
        author: "",
        dateposted: this.handledate(),
       });
    };
    render() {
       
         
        return (
            <div className="mt-2 pl-5 col-lg-6 cardcol">
               <Button  onClick={() => this.handleModalShowHide()} className="m-2 btn-primary">
                    New Article
                </Button>

                <Modal show={this.state.showHide}>
                    <Modal.Header closeButton onClick={() => this.closeModal()}>
                    <Modal.Title>New Article details</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    <Form  >
    <FormGroup>
      <Label>Title</Label>
      <Input type="text" placeholder="Enter Title"name="title" onChange={this.handleChange} value={this.state.activeItem.title} required/>
    </FormGroup>
  
    <FormGroup>
      <Label>Image</Label>
      <Input type="file" placeholder="Enter ID number" onChange={this.handleChange} name="photo"  required/>
    
      </FormGroup>
    <FormGroup>
      <Label>Article body.</Label>
      <textarea className="form-control" placeholder="Article here ..."name="body" onChange={this.handleChange} value={this.state.activeItem.body} required ></textarea>
    </FormGroup>
    <FormGroup>
      <Label>Author.</Label>
      <Input type="text" placeholder="Enter Author"name="author" onChange={this.handleChange} value={this.state.activeItem.author} required />
    </FormGroup>
    <FormGroup>
      
    <Input
                type="hidden"
                id="todo-description"
                name="dateposted"
                value={this.state.activeItem.dateposted}
                required
                onChange={this.handleChange}
                placeholder="Enter mobile Number"
              />
      </FormGroup>
            
                    <Button color="primary"className="mt-2 " type="submit" onClick={(e)=> this.handleSubmit(e)}>
      Submit
    </Button>  
    <Button color="primary ml-2"className=" m-2 mt-3" type=""onClick={() => this.closeModal()}>
      Close
    </Button>
    </Form>
    
    </Modal.Body>
    <Modal.Footer>
                    </Modal.Footer>
                </Modal>
            {this.state.requests.map((detail, id) =>  (
          <div key={id}>
              
              <Card style={{ width: '35rem' }}>
  <Card.Img variant="top" src="holder.js/100px180" />
  <Card.Body>
    <Card.Title>{detail.title}</Card.Title>
    <Card.Text>
    {detail.body}
    </Card.Text>
    <Button  className="m-2 btn-primary" onClick={() => this.editItem(detail)}>Edit</Button>
    <Button className='btn-primary' onClick={() => this.handleDelete(detail)}>Delete</Button>
  </Card.Body>
</Card>
  </div>
 
          )
          )}
         
        
          </div>
        )
    }
}

export default Articles
