import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Sideposts from './Sideposts';
import dateFormat from 'dateformat';
import './Blogdetail.css';
import { FacebookShareButton, FacebookIcon,TwitterShareButton,WhatsappShareButton,WhatsappIcon,TwitterIcon } from "react-share";
function Blogdetail(props) {
    const [blog, setblog] = useState([])
    const [newslug, setnewslug] = useState('')
    const [turl, setUrl] = useState('')
    useEffect(() => {
        const slug=props.match.params.id;
        const fetchdata=async()=>{
        const newslug=slug.substr(0,slug.indexOf("-"));
        setnewslug(newslug)
const res =await axios.get(`/api/article/`);
setblog(res.data);

        };
        fetchdata();
      },[props.match.params.id]);
     
    const createblog=()=>{
        if(blog.body){
        const text = blog.body;
        const newText = text.split('\n').map((item, i) => {
            return <p key={i}>{item}</p>;
        });
        
return newText
    
    }};
    
    return (
        <div className="row pin body">
        <div className="  container mt-3 col">
        {blog.filter(article => article.id ==newslug).map(filteredArticle => (
           
            <>
    
        <img src={filteredArticle.photo} height="250"width="100%"></img>
            <h6 className="display-2 titlesize">{filteredArticle.title}</h6>
            <label className ="title">Last Updated {dateFormat(filteredArticle.dateposted, "mmmm dS, yyyy")}</label>
            Share with:
            <span className="socialicon">
            <FacebookShareButton
    url={`http://sasaonline.co.ke/blog/${newslug}-${filteredArticle.title.replace(/\s+/g, '-')}`}
    quote={filteredArticle.title}
    hashtag="#techposts">
    <FacebookIcon logoFillColor="white" round={true} size={32} className="socialicon"/>
</FacebookShareButton>
</span>
<span className="socialicon">
<TwitterShareButton
    url={`http://sasaonline.co.ke/blog/${newslug}-${filteredArticle.title.replace(/\s+/g, '-')}`}
    title={filteredArticle.title}
    hashtag="#techposts">
    <TwitterIcon logoFillColor="white" round={true} size={32}/>
</TwitterShareButton>
</span>
<WhatsappShareButton
    url={`http://sasaonline.co.ke/blog/${newslug}-${filteredArticle.title.replace(/\s+/g, '-')}`}
    title={filteredArticle.title}
    hashtag="#techposts">
    <WhatsappIcon logoFillColor="white" round={true} size={32}/>
</WhatsappShareButton>

            <div className=" mt-2 body" />
            
            {filteredArticle.body}
            </>
  ))}
            </div>
            <div className="col-lg-5 ">
        <Sideposts/>
        </div>
            </div>
    )
}

export default Blogdetail
