import React, { Component } from 'react'
import { Table } from 'react-bootstrap';
import './Newrequests.css';
import axios from "axios";
import { Button } from 'bootstrap';
import { checkAuthenticated,load_user } from '../../actions/auth';
import {connect} from 'react-redux';
import {Link,Redirect} from 'react-router-dom';

export class Newrequests extends Component {
  state = {
    requests : [],
     id:"",
}
componentDidMount() {

    let data ;

    axios.get("/api/newjob/")
    .then(res => {
        data = res.data;
        this.setState({
            requests : data  
             
        });
        console.Blog(data) 
    })
    .catch(err => {})
    this.props.checkAuthenticated();
}
handleEdit=(id)=>{

    let formfield= new FormData()
    formfield.append('datecompleted',this.handledate(),)
  axios({
    method:'put',
    url:`/api/job/${id}/`,
    data:formfield,
  }).then((Response)=>{
   
  })
   alert("Record updated successfully")
}
handledate = () => {
  var today = new Date(),
  date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
  return date;
}
refreshList=()=>{
let data ;

    axios.get("/api/newjob/")
    .then(res => {
        data = res.data;
        this.setState({
            requests : data  
             
        });
        console.Blog(data) 
    })
    .catch(err => {})

}
    render() {
      var heading = ['Name', 'Id No.', 'Mobile No.','Job Type','message','date send','complete'];
      const { isAuthenticated } = this.props;
      if (isAuthenticated==false) {
          return (<Redirect to={'/login'} />);
      }
        return (
            <div className="mt-2 pl-5 col-lg-6 cardcol">
              <Table striped bordered hover size="sm" className="ml-5">
  <thead>
    <tr >
    {heading.map(head => <th>{head}</th>)}
    </tr>
  </thead>
 
                <tbody>     
                {this.state.requests.map(detail => (
                  <tr key={detail.id} >
                     <td className="col-2">{detail.names}</td>
                     <td class="col-1">{detail.idno}</td>
                     <td class="col-2">{detail.mobile}</td>
                     <td class="col-2">{detail.jobtype}</td>
                     <td class="col-1">{detail.message}</td>
                     <td class="col-1">{detail.datesend}</td>
                     <td class="col-1"><button className='btn-primary' onClick={()=>this.handleEdit(detail.id)} type="submit">Complete</button></td>
                   </tr>
             ))}
    </tbody>
    
     
           </Table> 
            </div>
        )
    }
}
const mapStateToProps=state=>({
  isAuthenticated:state.auth.isAuthenticated
  });

export default  connect(mapStateToProps,{checkAuthenticated,load_user})(Newrequests)

