import React,{useState,useEffect} from 'react';
import './Adminsidebar.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    NavLink,
    Link
  } from "react-router-dom";
import Home from './Home';
import Newrequests from './Newrequests';
import Crequests from './Crequests';
import Articles from './Articles';
import Newkra from './Newkra';
import Ckra from './Ckra';
import Login from './Login';
import Admin from './Admin';
import { checkAuthenticated,load_user } from '../../actions/auth';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom';


const Adminsidebar =({sideBaropen,closeSidebar, isAuthenticated,checkAuthenticated,load_user})=> {

  useEffect(()=>{
    checkAuthenticated();
    
      },[]) 
if(isAuthenticated==false){
 return <Redirect to='/login'/>}
 console.log()
    var testme=true;
    return (
        <Router>
              <div className='row'>
              <div className='col'>
        <div classname={sideBaropen?"sidebar-responsive":""} id="sidebar"> 
    
         
         <div className="sidebar_title"> 
         <div className="sidebar_img"> 
         <h1>Josetech</h1>
         <i className="fa fa-times" id="sidebaricon" onClick={()=>closeSidebar()}></i>
         </div>
         
         </div>
        
         <div className="sidebar_menu">
         <div className="sidebar_link active_menu_link">
         <i className="fa fa-home" ></i>
         
         <NavLink as={Link} to={"admin"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active">Dashboard </NavLink>
         <a href="#">  </a>
         </div>
         <h2>Job Requests</h2> 
         <div className="sidebar_link">  
         <i class="fa fa-paper-plane-o" aria-hidden="true"></i>
         <NavLink as={Link} to={"newrequests"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active"> New Requests</NavLink>
     
          </div>
          <div className="sidebar_link">  
          <i class="fa fa-list" aria-hidden="true"></i>
          <NavLink as={Link} to={"completedrequests"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active"> Completed Requests</NavLink>
          </div>

          <div className="sidebar_link">  
          <i class="fa fa-list" aria-hidden="true"></i>
          <NavLink as={Link} to={"newkra"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active"> Kra Requests</NavLink>
          </div>
        
          <div className="sidebar_link">  
          <i class="fa fa-list" aria-hidden="true"></i>
          <NavLink as={Link} to={"completedkra"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active">Kra Completed Requests</NavLink>
          </div>
          <h2>Blog</h2>
          <div className="sidebar_link">  
          <i class="fa fa-file-archive-o" aria-hidden="true"></i>
          <NavLink as={Link} to={"articles"} className="spacing py-1 linkstyle linkhover"exact={true} activeClassName="active">Articles</NavLink>
          </div>
          <div className="sidebar_logout">  
          <i className="fa fa-sign-out" aria-hidden="true"></i>
          <Link to="/Logout">Logout</Link>
        
         
          </div>

</div>

         </div>
         </div>
         <div className='col ml-5 content'>
      <Switch>
          <Route exact path="/">
            <Home />
          </Route> 
          <Route exact path="/newrequests">
            <Newrequests />
          </Route> 
          <Route exact path="/completedrequests">
            <Crequests />
          </Route> 
          <Route exact path="/articles">
            <Articles/>
          </Route> 
          <Route exact path="/newkra">
            <Newkra/>
          </Route> 
          <Route exact path="/completedkra">
            <Ckra/>
          </Route> 
          <Route exact path="/login">
            <Login/>
          </Route> 
          <Route exact path="/admin">
            <Admin/>
          </Route> 
          <Route path='/sasa-online-articles' component={Articles}/>
          </Switch>
          </div>
          
          </div>
         </Router>
        
    )
}

const mapStateToProps=state=>({
  isAuthenticated:state.auth.isAuthenticated
  });
export default connect(mapStateToProps,{checkAuthenticated,load_user})(Adminsidebar)

