import React, { useEffect, useState } from 'react';
import './App.css';
import Nav from './components/Nav';
import { BrowserRouter as Router,Switch,Route,useLocation } from 'react-router-dom';
import Home from './components/Pages/Home';
import Kra from './components/Pages/Kra';
import Business from './components/Pages/Business';
import Contact from './components/Pages/Contact';
import Sideposts from './components/Pages/Sideposts';
import Returns from './components/Pages/Returns';
import Footer from './components/Pages/Footer';
import Poster from './components/Pages/Poster';
import Wedding from './components/Pages/Wedding';
import Blog1 from './components/Pages/Blog1';
import Logo from './components/Pages/Logo';
import Login from './components/Pages/Login';
import Blogdetail from './components/Pages/Blogdetail';
import Adminsidebar from './components/Pages/Adminsidebar';
import Adminnav from './components/Pages/adminnav';

function App(props) {
   const [className, setclassName] = useState('');
   useEffect(() => {
      var isActive = window.location.pathname === '/poster';
          var className = isActive ? 'active1' : '';
          setclassName(className);
  console.log(className);
    },[]);
 
  return (
     
   <Router>
   { window.location.pathname != '/login'&& window.location.pathname != '/admin'&& window.location.pathname != '/articles' &&
   window.location.pathname != '/newrequests'&& window.location.pathname != '/completedrequests' && window.location.pathname != '/newkra'&& 
   window.location.pathname != '/completedkra'&& window.location.pathname != '/admin'&& 
      <Nav />}
      <div className="">
         
       
  
      <div className=''>
   {  window.location.pathname == '/admin'| window.location.pathname == '/newrequests' | window.location.pathname == '/completedrequests' 
   | window.location.pathname == '/newkra' | window.location.pathname == '/completedkra'| window.location.pathname == '/articles'&&  
      <Adminsidebar />}
      </div>
     
    
   
   <Switch>
     <Route path='/'exact component={Home}/>
     <Route exact path='/kra' component={Kra}/>
     <Route exact path='/returns' component={Returns}/>
     <Route exact path='/login' component={Login}/>
     <Route exact path='/business-card' component={Business}/>
     <Route exact path='/contact-us' component={Contact}/>
     <Route exact path='/poster' component={Poster}/>
     <Route exact path='/wedding-card' component={Wedding}/>
     <Route exact path='/blog' component={Blog1}/>
     <Route exact path='/logo' component={Logo}/>
     <Route exact path='/blog/:id' component={Blogdetail}/>
   </Switch>
  
   </div>
   { window.location.pathname != '/login'&& window.location.pathname != '/admin'&& window.location.pathname != '/articles' &&
   window.location.pathname != '/newrequests'&& window.location.pathname != '/completedrequests' && window.location.pathname != '/newkra'&& 
   window.location.pathname != '/completedkra'&& window.location.pathname != '/admin'  && 
      <Footer/>}
   
  
   </Router>
   
  );
}

export default App;
