import React, { Component } from 'react'
import { Table } from 'react-bootstrap';
import './Newrequests.css';
import axios from "axios";
export class Crequests extends Component {
  state = {
    requests : [],
   
}
componentDidMount() {

    let data ;

    axios.get("/api/cjob/")
    .then(res => {
        data = res.data;
        this.setState({
            requests : data  
             
        });
        console.Blog(data) 
    })
    .catch(err => {})
}
    render() {
      var heading = ['Name', 'Id No.', 'Mobile No.','Job Type','message','date send','completed date'];
        return (
          <div className="mt-2 pl-5 col-lg-6 cardcol">
          <Table striped bordered hover size="sm" className="ml-5">
<thead>
<tr>
{heading.map(head => <th>{head}</th>)}
</tr>
</thead>

            <tbody>     
            {this.state.requests.map(detail => (
              <tr key={detail.id}>
                 <td>{detail.names}</td>
                 <td>{detail.idno}</td>
                 <td>{detail.mobile}</td>
                 <td>{detail.jobtype}</td>
                 <td>{detail.message}</td>
                 <td>{detail.datesend}</td>
                 <td>{detail.datecompleted}</td>
               </tr>
         ))}
</tbody>

 
       </Table> 
        </div>
        )
    }
}

export default Crequests
