import React, { Component } from 'react'
import './Sideposts.css';
import axios from "axios";
import { Link } from 'react-router-dom';
import { checkActive } from '../../actions/auth';
import {connect} from 'react-redux';
import dateFormat from 'dateformat';
export class Sideposts extends Component {
  state = {
    details : [],
}
componentDidMount() {

    let data ;

    axios.get("/api/title/")
    .then(res => {
        data = res.data;
        this.setState({
            details : data  
             
        });
        console.Blog(data) 
    })
    .catch(err => {})
}
  render() {
    return (
      
      <div className="col-lg-7 mt-4  topposts">
        <h5 className="toptitle ml-1">RECENT POSTS</h5>
        {this.state.details.map((detail, id) =>  (
            <div key={id} onClick={this.props.checkActive()}>
        <b>
        <Link to={`/blog/${detail.id}-${detail.title.replace(/\s+/g, '-')}`}><label className="titlecursor">{detail.title}</label></Link>
          </b>
          <label className ="title">{dateFormat(detail.dateposted, "mmmm dS, yyyy")}</label>
          </div>
            )
        )}
        </div>
    )
  }
}

const mapStateToProps=state=>({
  isActive:state.auth.isActive
  });
export default connect(mapStateToProps,{checkActive})(Sideposts);
