import React, { useState,useEffect } from 'react';
import { Button } from './Button';
import { Link,NavLink } from 'react-router-dom';
import './Nav.css';
import Dropdown from './Dropdown';
import logo2 from './sasaonline1.png';
import {MobileView} from 'react-device-detect';
import { checkActive } from '../actions/auth';
import {connect} from 'react-redux';
const Nav=({checkActive,isActive})=> {
  const [click, setClick] = useState(false);
  const [person, setPerson] = useState(false);
  const [className, setclassName] = useState('');
  const [dropdown, setDropdown] = useState(false);
  const [url, seturl] = useState('0');
  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);
  const onMouseEnter = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(true);
    }
  };

  const onMouseLeave = () => {
    if (window.innerWidth < 960) {
      setDropdown(false);
    } else {
      setDropdown(false);
    }
  };
  useEffect(() => {
   checkActive();
   console.log(isActive);
  },[]);
  if(person){
    console.log(person) 
  }
   const checkLink=()=>{
     if(window.location.pathname == '/poster'|window.location.pathname == '/business-card' |window.location.pathname == '/wedding-card' |window.location.pathname == '/logo'){
       setclassName('active1')
     }
     else{
       setclassName('')
     }
   }
  return (
    <>
      <nav className='navbar2'>
       <li onClick={checkActive}><NavLink to='/'className='navbar-logo'  onClick={closeMobileMenu,checkLink}>
       <img src={logo2} height="70%"width="70%"></img>
          
        </NavLink></li> 
        <div className='menu-icon' onClick={handleClick}>
          <i class={click ? 'fas fa-times' : 'fas fa-bars'} />
          
        </div>
        <ul className={click ? 'nav-menu2 active' : 'nav-menu2'}>
          <li className='nav-item2'
          onClick={checkActive}>
            <NavLink exact to='/' className='nav-links2' activeClassName="activel" onClick={closeMobileMenu}>
              Home
            </NavLink>
          </li>
          <li className='nav-item2'
          onClick={checkLink}>
            <NavLink
              to='/blog' activeClassName="activel"
              className='nav-links2'
              onClick={closeMobileMenu}
            >
              Blog
            </NavLink>
          </li>
          <li className='nav-item2'
          onClick={checkActive}>
            <NavLink  to='/kra' className='nav-links2' activeClassName="activel" onClick={closeMobileMenu}>
              KRA Pin
            </NavLink>
          </li>
          
          <li
            className='nav-item2 mobileshow '
           
          >
            <NavLink exact
              to='#'
              className='nav-links2 design ' 
              
              onClick={closeMobileMenu}
            >
              Designs <i class='fas fa-caret-down' />
            </NavLink>
            
            
          </li>
          <li className='nav-item2 '
           onMouseEnter={onMouseEnter}
           onMouseLeave={onMouseLeave}
           onClick={checkActive}
          >
            <NavLink exact
              to='#'
              className='nav-links2 contact testme'
              
              activeClassName={isActive}
            > 
              Designs <i className='fas fa-caret-down' />
            </NavLink>
            {dropdown && <Dropdown />}
          </li>
          <li className='nav-item2 desktophide'>
            <NavLink
              to='/wedding-card' activeClassName="activel"
              className='nav-links2 contact'
              onClick={closeMobileMenu}
            >
              Wedding Card
            </NavLink>
          </li>
          <li className='nav-item2 desktophide'>
            <NavLink
              to='/business-card' activeClassName="activel"
              className='nav-links2 contact'
              onClick={closeMobileMenu}
            >
              Business Card
            </NavLink>
          </li>
          <li className='nav-item2 desktophide'>
            <NavLink
              to='/poster' activeClassName="activel"
              className='nav-links2 contact'
              onClick={closeMobileMenu}
            >
              Poster
        
            </NavLink>
          </li>
          <li className='nav-item2 desktophide'>
            <NavLink
              to='/logo' activeClassName="activel"
              className='nav-links2 contact'
              onClick={closeMobileMenu}
            >
              Logo
        
            </NavLink>
          </li>
          
          <li className='nav-item2'
          onClick={checkActive}>
            <NavLink
              to='/contact-us' activeClassName="activel"
              className='nav-links2'
              onClick={closeMobileMenu}
            >
              Contact Us
            </NavLink>
          </li>
        </ul>
        
      </nav>
      { person === '/4' && <img src={person} height=""width=""></img> }
    </>
  );
}
const mapStateToProps=state=>({
  isActive:state.auth.isActive
  });
export default connect(mapStateToProps,{checkActive})(Nav);